import React, { useState, useEffect, useRef } from 'react'
import Page from '../Page'
import loadApp from 'svelte/loadApp'

const SveltePage = () => {
  const el = useRef()

  useEffect(() => {
    if (el.current.innerHTML.length === 0) {
      loadApp('svelte', 'federation')
    }

    window.addEventListener('login', ({ detail }) => {
      console.log('em riba', detail.json())
    })
  })

  return (
    <Page title='Svelte Demo'>
      <div id='svelte' ref={el}></div>
    </Page>
  )
}


export default SveltePage
